let main = document.querySelector(".main")
let showStep2 = document.querySelector(".js-show-step2")
if(showStep2){
    showStep2.onclick = function () {
        main.classList.add("_show-step2")
    }
}

let showStep1 = document.querySelector(".js-show-step1")
if(showStep1){
    showStep1.onclick = function () {
        main.classList.remove("_show-step2")
    }
}

let cards = document.querySelectorAll(".m-card")

cards.forEach(card => {
    let text = card.querySelector("._text").innerHTML
    let modalText = document.querySelector("._modal-text")
    let buttons = card.querySelectorAll("._btn")
    buttons.forEach(btn => {
        btn.onclick = function () {
            modalText.innerHTML = text
            openModal("infoModal")
        }
    })
})
